                <p class="big">一、预算申报材料要求</p>
                <p>必须提交的预算申报材料：预算申报书正式书面文件统一使用A4纸，双面打印。</p>
                <p>预算申报书内容及装订顺序：</p>
                <div>
                  <ul class="txt1 clearfix">
                    <li>◆项目预算申报书封面</li>
                    <li>◆承诺书</li>
                    <li>◆项目有关报表A1~A7</li>
                  </ul>
                  <ul class="txt2">
                    <li>◆项目预算说明书</li>
                    <li>◆设备报价单（单价≥10万元的设备须提交此材料）*</li>
                  </ul>
                </div>
              </li>
              <li>
                <p class="big">二、预算编报原则及总体要求</p>
                <p class="black">1、预算编报原则</p>
                <ul class="retract">
                  <li>预算编报应当根据项目研究开发任务的实际需要，坚持目标相关性、政策相符性和经济合理性原则，科学、合理、真实地编制项目经费预算。</li>
                  <li>◆目标相关性原则：项目预算应与项目研究开发任务密切相关，预算的提出应该围绕项目目标、研究任务及技术路线等内容进行测算。</li>
                  <li>◆政策相符性原则：项目预算应符合有关财政预算管理、科技经费管理以及国家财务制度等的规定，符合科技经费管理改革要求。</li>
                  <li>◆经济合理性原则：项目预算需求应当结合研究开发的现有基础、前期投入和支撑条件，本着实事求是、经济合理、提高效益的原则测算提出。</li>
                </ul>
                <p class="black">2、预算编报的总体要求</p>
                <ul class="retract">
                  <li>(1)预算的组织编报：项目承担单位财务部门、项目负责人会同合作单位有关人员共同编制项目预算申报书，项目承担单位对预算编制的真实性负责。</li>
                  <li>(2)预算编报准备工作：在编制预算之前，应确认完成以下两方面的工作：</li>
                  <li>◆项目研究目标、内容、技术路线、研究周期、参加单位、参加人员及研究任务分解等内容已明确。</li>
                  <li>◆认真学习理解国家有关政策、制度的要求。</li>
                  <li>(3)预算编制的任务依据：编制预算必须以确定的研究任务为依据，项目名称、编号、负责人、承担（合作）单位、主要研究任务、实施周期等，不得随意变更。</li>
                  <li>(4)支出预算编制：各项支出预算不得简单按比例编列，不得编报不可预见费，也不得列入项目实施前发生的各项经费支出。</li>
                  <li>(5)预算编制的规范性要求：</li>
                  <li>◆金额单位和数据精度：预算数据以“万元”为单位，各支出科目预算数据精确到小数点后面两位，预算数据精确到个位。各类标准或单价以“元”为单位，精确到个位。外币需按人民银行公布的即期汇率折合成人民币。</li>
                  <li>◆编码与数据平衡关系：预算申报书中有关编码应填写准确，数据之间满足有关的平衡关系。</li>
                  <li>◆名称的规范性：所有项目和项目承担单位的名称，应填写正式全称，单位名称与单位公章必须一致，项目承担单位名称与其开户名称必须一致，如有不一致的情况，必须提供证明文件。</li>
                  <li>预算申报书中不同地方出现的相同设备、材料等实物信息应填写规范和统一的名称。</li>
                  <li>◆签字盖章：项目预算申报书必须经项目承担单位法定代表人、项目负责人、项目承担单位财务部门负责人以及预算编制人签字或盖章，并加盖项目主管部门、项目牵头单位公章。</li>
                  <li>预算申报书的填报内容：</li>
                  <li>项目预算申报书填报封面、项目基本情况表A1、任务参加人员基本情况表A2、任务预算表A3~A8，预算编制说明材料等（预算说明书和各种补充说明材料）。</li>
                  <li>(6)对预算编制说明材料的要求：</li>
                  <li>◆预算说明书是经费预算的一部分，必须按照规定格式、内容等要求详细编写预算说明书。</li>
                  <li>◆其他补充材料如设备报价单要求在报送时一并提交。</li>
                </ul>
              </li>
            </ol>
          </h2>
