        <!-- 第六个tab -->
        <div v-show="activeIdx === 5" label="推荐及审核意见" name="sixth" class="sixth">
          <!-- 创新工程项目预算说明书 -->
          <p class="headline">创新工程项目预算说明书</p>
          <p class="intro">一、对承担单位和相关部门承诺提供的支撑条件进行详细说明，并针对项目实施可能形成的科技条件资源和成果，提出社会共享的方案。</p>
          <div class="textarea">
            <div>
              <el-form-item>
                <dt-textarea :value.sync="textarea1" :maxNumber="2000"></dt-textarea>
              </el-form-item>
            </div>
            <!-- <ul>
            <li class="fl">
              <span>项目摘要：</span>
            </li>
            <li class="fl">
              <el-form-item>
                
                <dt-textarea :value.sync="textarea" :maxNumber="2000"></dt-textarea>
              </el-form-item>
            </li>
            </ul>-->
          </div>
          <p class="intro">二、对各承担单位经费安排进行详细说明，应包括在项目中承担的主要任务、经费需求、测算理由及各科目预算分配等方面详细内容。</p>
          <div class="textarea">
            <div>
              <el-form-item>
                <dt-textarea :value.sync="textarea2" :maxNumber="2000"></dt-textarea>
              </el-form-item>
            </div>
          </div>
          <p class="intro">三、对该项目各科目支出的主要用途、与项目研究的相关性及测算方法、测算依据进行详细分析说明。</p>
          <div style="paddingLeft:16px;">
            <p style="font-size: 16px;color: #686868;marginBottom:14px">（一）直接经费</p>
            <ul>
              <li style="paddingLeft:40px">
                <p class="intro" style="max-width:70%">
                  1. 设备费：请说明购置或试制单台价值10万元以上设备与项目研究任务的关系和必要性、现有同样设备的利用情况、设备用途、设备与现有设备的配套情况、
                  设备使用率、设备拟安置单位、购置设备的开放共享方案、试制设备的方案和成本构成等等，另外，购置单价10万元以上的进口设备需提供三家以上产品报
                  价单及其联系电话的详细资料；当单价10万元以下设备的购置或试制总量较大时，需提供说明。
                </p>
                <div>
                  <div class="textarea">
                    <div>
                      <el-form-item>
                        <dt-textarea :value.sync="textarea311" :maxNumber="2000"></dt-textarea>
                      </el-form-item>
                    </div>
                  </div>
                </div>
              </li>

              <li style="paddingLeft:40px">
                <p
                  class="intro"
                  style="max-width:70%"
                >2. 材料费：请说明购置的各种材料与项目研究任务的关系和必要性、所需数量的测算依据，并详细列示各种材料的名称、购买单价、购买数量以及总金额。</p>
                <div>
                  <div class="textarea">
                    <div>
                      <el-form-item>
                        <dt-textarea :value.sync="textarea312" :maxNumber="2000"></dt-textarea>
                      </el-form-item>
                    </div>
                  </div>
                </div>
              </li>
              <li style="paddingLeft:40px">
                <p
                  class="intro"
                  style="max-width:70%"
                >3. 测试化验加工费：请说明预算的各种测试化验与加工项目与项目研究任务的相关性和必要性、测试化验加工次数的测算依据以及委托该单位的理由等</p>
                <div>
                  <div class="textarea">
                    <div>
                      <el-form-item>
                        <dt-textarea :value.sync="textarea313" :maxNumber="2000"></dt-textarea>
                      </el-form-item>
                    </div>
                  </div>
                </div>
              </li>
              <li style="paddingLeft:40px">
                <p
                  class="intro"
                  style="max-width:70%"
                >4. 燃料动力费：请说明预算的各种燃料与项目研究任务的相关性和必要性、所需数量、单价的测算依据等。</p>
                <div>
                  <div class="textarea">
                    <div>
                      <el-form-item>
                        <dt-textarea :value.sync="textarea314" :maxNumber="2000"></dt-textarea>
                      </el-form-item>
                    </div>
                  </div>
                </div>
              </li>
              <li style="paddingLeft:40px">
                <p class="intro" style="max-width:70%">
                  5. 差旅费、会议费、国际交流与合作费（不超总预算10%的不需要提供预算测算依据）：差旅费：请说明预算的各项出差任务与项目研究任务的关系和必要性，
                  以及出差次数、出差标准的预算依据。会议费：请说明预算的各种会议与项目研究任务的关系和必要性，以及预算的会议次数、会议标准的预算依据。国际合
                  作与交流费：请说明预算的各项国际合作与交流与项目研究任务的关系和必要性，并详细列示出访或邀请来华专家的国家或地区名称、机构名称、人数、天
                  数、标准的预算依据。
                </p>
                <div>
                  <div class="textarea">
                    <div>
                      <el-form-item>
                        <dt-textarea :value.sync="textarea315" :maxNumber="2000"></dt-textarea>
                      </el-form-item>
                    </div>
                  </div>
                </div>
              </li>
              <li style="paddingLeft:40px">
                <p
